*{
box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    padding: 0;
background-color: #111;
height: fit-content;
max-height: 100vh;
min-height: 100vh;



}


.acc_container{
  background: rgba(230, 8, 8, 0.137);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.61);
 

  border: 1px solid rgba(255,255,255,0.3);
  padding: 20px;
  min-width: 300px;
  position: fixed;
 top: 29%;
 left: 5%;
 right: 5%;
  margin: auto;
  z-index: 17;
color: black;
align-self: center;
justify-content: center;
animation: close 1s 1;
-webkit-animation: close 1s 1;
}


@keyframes close {
  
from{
transform: scale(0.3);
transition: 1s;
}

to{
transform: scale(1);
transition: 1s;
}
}




@-webkit-keyframes close{
  from{
    -webkit-transform: scale(0.3);
  transition: 1s;
  }

  to{
    -webkit-transform: scale(1);
  transition: 1s;
  }
}


.tran{
  transform: translate(0, -20px);
  font-size: 1em !important;
  
}
.container_inputs{
  position: relative;
  min-width: 290px;
  margin: 2rem 0;
  color: black;
}

.container_inputs label{
  position: absolute;
  font-size: 1.6em;
  z-index: 1;
  color:black;
  
  transition: .5s;
}

.container_inputs input{
position: relative;
border: none;
border-bottom: 1px solid black;
background: transparent;
width:99%;
font-size: 1.4em;
box-shadow: none;
outline: none;
z-index: 2;
padding-bottom: 5px;
color: rgb(0, 0, 0);

}

.imgbi{
  object-fit:initial;
height: 110%;
width:100%;
margin: auto;
position: relative;
left:0%;
right:10%;
}
.btn_submit{

  width: 100px;
  height: 40px;
  outline: none;
  border-radius: 5px;
  border: 1px solid black;
  background-color: transparent;
align-self: center;
justify-self: center;
margin: auto;
margin-left: 30%;
}

.btn_submit:hover{
 border-color: aqua;
 color: white;
}

.trans{
  transform: translate(0, -20px);
  font-size: 1em !important;
 
}

.exit{
